<template>
	<!-- 搜索 -->
	<section id="searchMG" style="display: none;">
		<div class="search-box">
			<form action="javascript:return true">
			    <input @keypress="searchKeyPress" class="search-box-text search-font" type="search" placeholder="请输入"/>
			</form>
		</div>
		<div class="search-content" @click="searchShow()">	
			<ul class="search-init-ul">
				<li class="search-init-li" v-for="data in datas">
					<router-link :to="'/comic?url='+data.url" class="item-link">
						<div class="item-cover-div">
							<img :src="data.cover" alt="" class="item-cover">
						</div>
						<div class="item-info">
							<strong class="item-title">{{data.name}}</strong>
							<small class="item-class">{{data.latest}}</small>
						</div>
					</router-link>
				</li>
			</ul>
		</div>
	</section>
</template>

<script>	
export default {
	name: 'Search',
	data(){
		return {
			searchText: null,
			datas: [],
			searchdatas: [],
			mhname: this.baseUrl.mhname
		}
	},
	created() {
		
	},
	methods:{
		searchShow: function(){
			$("#searchMG").hide();
		},
		/*回车搜索*/
		searchKeyPress: function(e){
		  if(e.which == 13){
		    this.searchText = $(".search-box-text").val();
		    if(this.searchText != null){
		      this.searchdatas = this.$options.methods.getDatas( this.searchText, this.mhname);		
		      if(this.searchdatas.length > 1){
		        //返回给ui
		        for(var i = 0; i < this.searchdatas.length; i++){
		          if(this.searchdatas[i].name != null && this.searchdatas[i].url != null && this.searchdatas[i].cover != null){
					this.datas.push(this.searchdatas[i]);
		          }
		        }               
		      }
		    }
		  }   
		},
		getDatas: function (url, mhname){
		  var defer = $.Deferred();
		  var arrdatas = new Array();		  
		  
		  $.ajax({
		    url: mhname+url,
		    type: "get",
		    async: false,
		    dataType: "json",
		    success: function(res){           
		      if(res.code == 0){
		        defer.resolve(res);
		        $.when(defer.promise()).done(function(res){
		          if(res.list != ""){
		            $.each(res.list, function(index, res){
		              if(res.url != null){
		                arrdatas.push(res);
		              }
		            })
		          }       
		        })
		      }
		    },
		    error: function(err, xmlerr, htmlerr){
		      console.log(xmlerr);
		    }
		  });
			return arrdatas;
		},		
	},
	mounted() {
		
	}
}
</script>

<style>
</style>



