<template>
	<div>
		<Header :title="titleInfo.name"></Header>
		<!-- 漫画详细信息 -->
		<section id="titleInfo" class="ContentBase clearfix">
			<div class="comic-info-div">
				<div class="comic-cover">
					<img :src="titleInfo.cover" alt="" class="item-cover">
				</div>
				<div class="comic-info">
					<strong class="info-title">{{ titleInfo.name }}</strong>
					<small class="info-num">{{ titleInfo.tag }}</small>
					<!-- <small class="info-class">{{ titleInfo.latest }}</small> -->
					<small class="info-time">{{ titleInfo.time }}</small>
					<small class="info-descript">{{ titleInfo.introduce }}</small>
					<button @click="readClick" class="read-btn" type="button">点击阅读</button>
				</div>
			</div>
		</section>
		<!-- 章节列表 -->
		<section id="chapter" class="ContentBase clearfix">
			<div class="main-content">
				<h1>章节列表</h1>
				<ul class="chapter-init-ul">
					<li class="chapter-init-li" v-for="x in datas">
						<router-link :to="'/chapter?chapter='+x.url+'&page='+x.page+'&url='+pathName" class="chapter-link">{{ x.num }}</router-link>
					</li>
				</ul>
			</div>
		</section>
		<Youlike></Youlike>
		<Footer></Footer>
	</div>
</template>

<script>
	import Header from '../commons/Header_Common.vue'
	import Footer from '../commons/Footer.vue'
	import Youlike from '../commons/Youlike.vue'
	
export default {
	name: 'Comic',
	data() {
		return {
			datas: [],			
			titleInfo: null,
			pathName: "",
			pathName2: ""
		}
	},
	created() {		
		this.pathName = this.$route.query.url;
		this.pathName2 = this.baseUrl.mhurl1 + this.pathName;
		this.datas = this.getDatas( this.pathName2 );						
		this.commonMethods.deferload();
	},
	watch: {
		"$route": function(){
			this.pathName = this.$route.query.url;
			this.pathName2 = this.baseUrl.mhurl1 + this.pathName;
			this.datas = this.getDatas( this.pathName2 );						
			this.commonMethods.deferload();
			this.commonMethods.toTop()
		}
	},
	methods: {
		readClick : function(){			
			if(this.datas.length > 1){
				//返回给ui	
				//$("#chapter").attr("ng-init", "datas=["+datas+"]");				
							
				var data1 = this.datas[0];
				this.$router.push({path: "/chapter?chapter="+data1.url+"&page=1&url="+this.pathName});
			}
		},
		getDatas: function (url){
			var defer = $.Deferred();
			var arrdatas = new Array();
			var titleData = this.titleInfo;
							
			$.ajax({
				url: url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){
						defer.resolve(res);
						$.when(defer.promise()).done(function(res){
							// $("title").text(res.data.name + " - RManGa");
							// $(".title-name").text(res.data.name);
							// res.data.cover = res.data.cover.replace(/http:/,"");
							// res.data.cover = res.data.cover.replace(/https:/,"");
							// res.data.cover = "http:" + res.data.cover;
							titleData = res.data;
							if(res.list != ""){
								$.each(res.list, function(index, item){
									item.page = index;										
									if(item.url != null){
										if(item.page == 0){
											item.page += 1;
										}											
										arrdatas.push(item);
									}
								})
							}
						})
					// }else{
					// 	//关闭加载
					// 	$(".loading-gif").hide();
					// 	$(".comic-loading").append("<p style='position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font-size: .32rem; color: #c7c7c7;'>"+ res.message +"</p>");
					// }
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			
			this.titleInfo = titleData;
			return arrdatas;
		}		
	},
	components: {
		Header,
		Footer,
		Youlike
	},
	mounted(){
		this.commonMethods.toTop();
	}
}
</script>

<style>
</style>



