<template>
	<div>
		<Header :title='title'></Header>
		<Loader :isShow="loader"></Loader>
		<!-- 漫画列表 -->
		<section id="comicList" class="ContentBase clearfix">			
			<ul class="rank-init-ul">
				<li class="rank-init-li" v-for="x in datas">
					<router-link :to="'/comic?url='+x[0].url" class="rank-item-link">
						<div class="item-cover-div">
							<img :src="x[0].cover" alt="" class="item-cover">
						</div>
						<div class="item-info">
							<strong class="item-title">{{ x[0].name }}</strong>
							<!-- <small class="item-num">23400</small> -->
							<small class="item-class" style="color: #d97685;">{{ x[0].author }}</small>
							<small class="item-time">{{ x[0].status }}</small>
							<small class="item-time">{{ x[0].tag }}</small>
						</div>
					</router-link>
				</li>
			</ul>
		</section>
		<section class="load-more">
			<div class="status-loading">
				<p class="loading-info">加载中...</p>
			</div>
			<div class="status-over" style="display: none;">~已经到底了~</div>
			<div @click="IsShow" class="status-btn" style="display: none; width: 100%; height: 100%;">
				<button type="button" style="display: block; width: 100%; height: 100%; background: none; border: none;">点击下一章</button>
			</div>
		</section>
		<Footer></Footer>
	</div>
</template>

<script>
import Header from '../commons/Header_Common.vue'
import Footer from '../commons/Footer.vue'
import Loader from '../commons/Loader.vue'
	
export default {
	name: 'ComicList',
	data(){
		return{
			datas: [],
			shuye: 1,
			shuyeAll: 0,
			pathName: "",
			pathName2: "",
			ref: [],
			title: "",
			loader: true
		}
	},
	computed:{
		
	},
	created(){
		//获取漫画类型参数
		this.pathName = this.$route.query.class;
		this.shuye = 1; //初始化页面
		this.shuyeAll = 0; //总页面
		
		this.pathName2 = this.baseUrl.mhlb + this.pathName + "-1";
		
		this.datas = this.getDatas(this.pathName2); //内容赋值
		
		if(this.datas.length > 0){
			this.loader = false;
		}
				
	},
	watch: {
		"$route": function(){
			this.commonMethods.toTop();
			this.loader = true;
			//获取漫画类型参数
			this.pathName = this.$route.query.class;
			this.shuye = 1; //初始化页面
			this.shuyeAll = 0; //总页面
			
			this.pathName2 = this.baseUrl.mhlb + this.pathName + "-1";
			
			this.datas = this.getDatas(this.pathName2); //内容赋值
			
			if(this.datas.length > 0){
				this.loader = false;
			}
			
			switch(this.pathName){
				case 'shaonianrexue':
					this.title = "少年热血";
					break;
				case 'lianaishenghuo':
					this.title = "恋爱生活";
					break;
				case 'shenghuomanhua':
					this.title = "生活漫画";
					break;
				case 'wuxiagedou':
					this.title = "武侠格斗";
					break;
				case 'kongbulingyi':
					this.title = "恐怖灵异";
					break;
				case 'danmeirensheng':
					this.title = "耽美人生";
					break;
				case 'shaonvaiqing':
					this.title = "少女爱情";
					break;
				case 'kehuanmohuan':
					this.title = "科幻魔幻";
					break;
				case 'jingzhengtiyu':
					this.title = "竞争体育";
					break;
				case 'baoxiaoxiju':
					this.title = "爆笑喜剧";
					break;
				case 'zhentantuili':
					this.title = "侦探推理";
					break;
			}
		}
	},
	methods:{
		getData: function (name){
			var defer = $.Deferred();
			var arrdatas = new Array();				
			
			$.ajax({
				url: name,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){
					defer.resolve(res);
					$.when(defer.promise()).done(function(res){
						arrdatas.push(res.list[0]);
					})
				},
				error: function(err, xmlerr, htmlerr){
					console.log(htmlerr);
				}
			});
			
			return arrdatas;
		},		
		getDatas: function (url){
			var defer = $.Deferred();
			var arrdatas = new Array();
			
			var loadData = this.baseVar.loadData;
			var arrPageName = this.baseVar.arrPageName;
			var manhuaNum = this.baseVar.manhuaNum;
			var shuye = this.shuye;
			var shuyeAll = this.shuyeAll;
			
			$.ajax({
				url: url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){						
					if(res.code == 0){
						defer.resolve(res);
						//关闭加载
						$(".comic-loading").hide();
						$.when(defer.promise()).done(function(res){
							if(res.list != ""){
								if(Number(res.list[0].number) > 1){
									shuye = Number(res.list[0].pages);
									shuyeAll = Number(res.list[0].dpages);
									$.each(res.list, function(index, res){
										if(res.url != null){
											arrPageName.push(res.name); //添加姓名进组
										}
									});									
									manhuaNum = 10;
								}else{
									//关闭加载		
									$(".loading-gif").hide();
									$(".comic-loading").append("<p style='position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font-size: .32rem; color: #c7c7c7;'>"+ res.message +"</p>");
								}
							}else{
								//关闭加载		
								$(".loading-gif").hide();
								$(".comic-loading").append("<p style='position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font-size: .32rem; color: #c7c7c7;'>"+ res.message +"</p>");
							}														
						})
					}
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			
			for(var i = 0; i < 10; i++){
				arrdatas = this.$options.methods.getData(this.baseUrl.mhname+arrPageName[i]);
				loadData.push(arrdatas);
			}
			
			this.baseVar.loadData = loadData;
			this.baseVar.arrPageName = arrPageName;
			this.baseVar.manhuaNum = manhuaNum;
			this.shuye = shuye;
			this.shuyeAll = shuyeAll;
			
			return loadData;
		},
		//无限下来列表
		scrollFn: function (){
		    //真实内容的高度
		    var pageHeight = Math.max(document.body.scrollHeight,document.body.offsetHeight);
		    //视窗的高度
		    var viewportHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0;
		    //隐藏的高度
		    var scrollHeight = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
		    if(pageHeight - viewportHeight - scrollHeight < 50){//如果满足触发条件，执行		        	
		        if(this.baseVar.isComplete){
		        	$(".status-loading").hide();
		        	$(".status-btn").show();
		        }else{
		        	$(".status-loading").show();
		        	this.ref = this.$options.methods.showContent(this.datas, this.baseUrl.mhname, this.$options.methods.getData, this.baseVar.arrPageName, this.baseVar.isComplete, this.baseVar.manhuaNum);				
					if(this.ref.length > 0){
						this.datas = this.ref[0];
						this.baseVar.isComplete = this.ref[1];
						this.baseVar.manhuaNum = this.ref[2];
					}
		        }
		    }
		},
		showContent: function (datas, mhname, getData, arrPageName, isComplete, manhuaNum){
			var newmanhuaNum = manhuaNum+10;
			var ret = []; //需要返回刷新的数据
			// if(datas.length >= 60){
			// 	datas.splice(0, 10);  //清除加载数据数组，重新加载数据组
			// }			
			for(manhuaNum; manhuaNum < newmanhuaNum; manhuaNum++){					
				if(manhuaNum >= arrPageName.length){
					isComplete = true;
					manhuaNum = newmanhuaNum;
				}else{
					if(arrPageName[manhuaNum] != undefined){
						datas.push(getData(mhname+arrPageName[manhuaNum]));
					}					
				}
			}
			
			ret.push(datas);
			ret.push(isComplete);
			ret.push(manhuaNum);
			
			return ret;	
		},
		IsShow :function(){
			this.baseVar.arrPageName.splice(0, this.baseVar.arrPageName.length);
			this.datas.splice(0, this.datas.length);			
			this.baseVar.manhuaNum = 0;
			this.baseVar.isComplete = false;
			if(this.shuye <= this.shuyeAll){
				window.location.href = location.pathname + "#/comiclist/?class=" + this.pathName.split("-")[0] + "-" + (++this.shuye);
			}else{
				$(".status-btn").hide();
				$(".status-over").show();
			}
		}	
	},
	mounted() {
		this.commonMethods.toTop();
		$(window).bind("scroll", this.scrollFn);//绑定滚动事件

		switch(this.pathName){
			case 'shaonianrexue':
				this.title = "少年热血";
				break;
			case 'lianaishenghuo':
				this.title = "恋爱生活";
				break;
			case 'shenghuomanhua':
				this.title = "生活漫画";
				break;
			case 'wuxiagedou':
				this.title = "武侠格斗";
				break;
			case 'kongbulingyi':
				this.title = "恐怖灵异";
				break;
			case 'danmeirensheng':
				this.title = "耽美人生";
				break;
			case 'shaonvaiqing':
				this.title = "少女爱情";
				break;
			case 'kehuanmohuan':
				this.title = "科幻魔幻";
				break;
			case 'jingzhengtiyu':
				this.title = "竞争体育";
				break;
			case 'baoxiaoxiju':
				this.title = "爆笑喜剧";
				break;
			case 'zhentantuili':
				this.title = "侦探推理";
				break;
		}
	},
	components: {
		Header,
		Footer,
		Loader
	}
}
</script>

<style>
</style>



