<template>
	<header>
		<div class="head_container">
			<div class="head_top">					
				<a class="goback" href="javascript: history.back();"></a>
				<span>{{ title }}</span>
				<a @click="addSearch()" class="search-link" href="javascript: void(0);"></a>
			</div>
		</div>
		<Search></Search>
	</header>
</template>

<script>
import Search from '../commons/Search.vue'	
	
export default {
	name: 'Header_Common',
	props: ['title'],
	methods:{
		addSearch: function(){
			$(".head_container").next().show();
		}
	},
	components:{
		Search
	}
}
</script>

<style>
</style>



