export default{
	install(Vue, options){
		Vue.prototype.loaderShow = {
			shows: false
		},
		Vue.prototype.baseVar = {
			isComplete : false, //当前页是否完成
			manhuaNum : 0, //单页漫画的数量
			arrPageName : [] ,//单页所有姓名
			loadData : [] //加载过的数据
		},
		Vue.prototype.baseUrl = {
			mhname: 'https://api.pingcc.cn/?mhname=',
			mhlb: 'https://api.pingcc.cn/?mhlb=',
			mhurl1: 'https://api.pingcc.cn/?mhurl1=',
			mhurl2: 'https://api.pingcc.cn/?mhurl2='
		},
		Vue.prototype.localManga = {
			namelist: [
			      {"name" : "斗破苍穹"},
			      {"name" : "绝世唐门"},
			      {"name" : "斗罗大陆"},
			      {"name" : "凤逆天下"},
			      {"name" : "风起苍岚"},
			      {"name" : "武动乾坤"},
			      {"name" : "神印王座"},
			      {"name" : "斗罗大陆3龙王传说"},
			      {"name" : "斗破苍穹之大主宰"},
			      {"name" : "穿越西元3000后"},
			      {"name" : "血族禁域"},
			      {"name" : "妖神记"},
			      {"name" : "寻找前世之旅"},
			      {"name" : "地府我开的"},
			      {"name" : "X龙时代"},
			      {"name" : "元尊"},
			      {"name" : "完美世界"},
			      {"name" : "三眼哮天录"},
			      {"name" : "我被总裁黑上了！"},
			      {"name" : "不嫁总裁嫁男仆"},
			      {"name" : "重生豪门之强势归来"},
			      {"name" : "姻缘宝典"},
			      {"name" : "妃夕妍雪"},
			      {"name" : "斗罗大陆外传神界传说"},
			      {"name" : "王爵的恋爱物语"},
			      {"name" : "网游之近战法师<"},
			      {"name" : "偷星九月天"},
			      {"name" : "奇怪的苏夕"},
			      {"name" : "豪门天价前妻"},
			      {"name" : "因爱宠你"},
			      {"name" : "很纯很美好"},
			      {"name" : "星武神诀"},
			      {"name" : "绝世武神"},
			      {"name" : "灵剑尊"},
			      {"name" : "明星是血族"},
			      {"name" : "逆天邪神"},
			      {"name" : "皇后娘娘的五毛特效"},
			      {"name" : "渣男总裁别想逃"},
			      {"name" : "女子学院的男生"},
			      {"name" : "帝豪老公太狂热"},
			      {"name" : "国民老公带回家：偷吻55次"},
			      {"name" : "中华神医"},
			      {"name" : "冷情殿下：捉弄小萌妻"},
			      {"name" : "神医嫡女"},
			      {"name" : "万丈光芒不及你"},
			      {"name" : "吸血鬼男神"},
			      {"name" : "王牌校草"},
			      {"name" : "骑士幻想夜"}
			  ]
		},
		Vue.prototype.commonMethods = {
			//顶部向上
			toTop: function(){
				window.scrollTo(0, 0);
			},
			//随机数
			numRandom: function(long, geshu){
			  var num = new Array();
			  var newnum = 0;
			  var isok = false;
			  for(var i = 0; i < geshu; i++){                 
			    do{
			      var count = 0;
			      newnum = Math.floor((Math.random()*long));
			      for(var i = 0; i < num.length; i++){              
			        if(newnum != num[i]){
			          count++;                
			        }
			      }
			      if(count != num.length){
			        isok = true; 
			      }else if(count == num.length){
			        isok = false;
			      }
			    }while(isok);
			    num.push(newnum);         
			  }
			  return num;
			},
			// 获取地址栏参数
			getQueryVariable: function (variable)
			{
			   var query = window.location.search.substring(1);			   
			   var vars = query.split("&");
			   for (var i=0;i<vars.length;i++) {
			        var pair = vars[i].split("=");
			        if(pair[0] == variable){return pair[1];}
			   }
			   return(false);
			},
			//延迟加载资源
			deferload: function (){
			  window.onload = function(){
			      var url = new Array();
			      var srctext = "";
			      var imgAll = document.getElementsByTagName("img");
			
			      for(var i = 0; i < $(".slcf_container img").length; i++){
			        srctext = imgAll[i].getAttribute("src");
			        url.push(srctext.replace("http:", "https:"));
			      }
			
			      var load_count = 1;   // 定义变量记录加载次数
			      // 设置定时器，3秒刷新一次
			      var timer = setInterval(function() {
			        //var img_id = document.getElementById("img1"); //若是jq，则直接将此代码换成 var img_id = $("#img_id"); 即可。
			        for(var i = 0; i < imgAll.length; i++){
			          var imgObj = new Image();
			          imgObj.setAttribute("src", imgAll[i].getAttribute("src"));                      //若是jq，则直接将此代码换成 imgObj.src = img_id.attr("src"); 即可。
			          console.log('当前资源链接：' + imgAll[i].src)
			          console.log("大小：" + imgObj.width + "*" + imgObj.height);
			          if (imgObj.width > 0 && imgObj.height > 0) {
			            console.log("Loading done。。。");
			            clearInterval(timer);     // 加载完成，清除定时器
			          } else {
			            if (load_count >= 2) {
			              // 替换后的链接仍加载失败，删除
			              imgAll[i].remove();
			              console.log("链接资源不存在，加载失败，删除。。。。。。");
			              clearInterval(timer);
			            } else {
			              console.log("First time load fail, Begin replace。。。。。。");
			              // 加载失败，替换img标签的src属性链接
			              imgAll[i].setAttribute("src", url[i]);
			              load_count++;
			              console.log('替换后：' + imgAll[i].getAttribute("src"));
			            }
			          }
			        }        
			      }, 3 * 1000);
			    };
			}
		}
	}
}


