import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Header from '@/components/commons/Header.vue'   //头部
import Footer from '@/components/commons/Footer.vue'   //底部导航
import Banner from '@/components/commons/Banner.vue'   //轮播图导航
import RecommenedList from '@/components/commons/RecommenedList.vue'   //动画列表推荐

import Home from '@/components/index/Home.vue'  //首页
import Category from '@/components/index/Category.vue'   //分类
import Rank from '@/components/index/Rank.vue'   //排行
import Comic from '@/components/index/Comic.vue'   //章节列表
import Chapter from '@/components/index/Chapter.vue'   //漫画阅读工具
import ComicList from '@/components/index/ComicList.vue'  //漫画列表

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
	  name: 'Home',
      component: Home
    },
	{
	  path: '/home',
	  name: 'Home',
	  component: Home
	},
	{
	  path: '/category',
	  name: 'Category',
	  component: Category
	},
	{
	  path: '/rank',
	  name: 'Rank',
	  component: Rank
	},
	{
	  path: '/comic',
	  name: 'Comic',
	  component: Comic,	  
	},
	{
	  path: '/chapter',
	  name: 'Chapter',
	  component: Chapter
	},
	{
	  path: '/comiclist',
	  name: 'ComicList',
	  component: ComicList
	}
  ]
})



