<template>
	<div class="slcf_container">
		<!-- 悬浮栏区域 上 -->
		<section class="float-bar top" style="display: none;">
			<a @click="goback" href="#" class="goback"></a>
			<router-link :to="'/comic?url='+pathName2" class="godescript">详</router-link>
			<router-link to="/home" class="gohome">家</router-link>
			<p class="float-bar-info">第<span>{{page}}</span>话-<span>{{index}}</span>/<span>{{datas.length}}</span></p>
		</section>
		<!-- 漫画图片区域 -->
		<section class="comic-pic-list-all clearfix" id="open-ctrl">
			<router-link :to="prevbtn" class="nav-ctrl-btn prev" data-can-read="">点击加载上一章</router-link>			
			<!-- 漫画图片插入 -->		
			<section class="comic-pic-insert">
				<ul class="comic-pic-ul">
					<li class="comic-pic-li" v-for=" (x, index) in datas"  :data-cid="page" :data-index="index+1" >
						<div class="comic-pic-box">
							<img :src="x.img" alt="" class="comic-pic">
						</div>
					</li>
				</ul>
			</section>
			<router-link :to="nextbtn" class="nav-ctrl-btn next" data-can-read="">点击加载下一章</router-link>			
		</section>
		<!-- 悬浮栏 下 -->
		<section class="float-bar bottom" style="display: none;">
			<router-link :to="prevbtn" class="chapter-prev-btn" type="button">上一章</router-link>
			<router-link :to="nextbtn" class="chapter-next-btn" type="button">下一章</router-link>
		</section>
		<!-- 漫画控制面板 -->
		<section class="comic-ctrl-box" style="display: none;">
			<div class="comic-back-box"></div>
		</section>
	</div>
</template>

<script>
export default {
	name: 'Chapter',
	data(){			
		return{
			datas: [], //内容
			pages: [], //章节
			titleInfo: null,
			prevPage: null,
			nextPage: null,
			pathName: "",
			pathName2: "",
			page: 0,
			index: 0,
			prevbtn: "",
			nextbtn: ""
		}
	},
	created(){
		this.init();
	},
	watch: {
		'$route': 'init'
	},
	methods: {
		init: function(){
			//获取漫画类型参数
			this.pathName = this.$route.query.chapter;
			this.pathName2 = this.$route.query.url;
			this.page = this.$route.query.page;
			
			//加载内容
			var path = this.baseUrl.mhurl2 + this.pathName;
			this.datas = this.$options.methods.getDatas1(path);	
			
			// //加载章节
			var path2 = this.baseUrl.mhurl1 + this.pathName2; 
			this.pages = this.getDatas2(path2);
			
			//上下章节			
			this.prevPage = Number(this.page)-1;
			this.nextPage = Number(this.page)+1;
			
			//如果是第一章
			if(this.prevPage == 0){
				$(".prev").text("已经是第一章")
				$(".chapter-prev-btn").css("background-color", "#ddd");
				this.prevbtn = "#";
			}else{
				$(".prev").css("z-index", 20000);
				this.prevbtn = '/chapter?chapter='+this.pages[this.prevPage].url+'&page='+this.prevPage+'&url='+this.pathName2;			
			}			
			
			//如果是最后一章
			if(this.nextPage > this.pages.length){				
				$(".next").text("已经是最后一章");
				$(".chapter-next-btn").css("background-color", "#ddd");
				this.nextbtn = "#";
			}else{
				$(".next").css("z-index", 20000);
				this.nextbtn = '/chapter?chapter='+this.pages[this.nextPage].url+'&page='+this.nextPage+'&url='+this.pathName2;
			}
						
			this.commonMethods.toTop();
		},
		getDatas1: function (url){
			var defer = $.Deferred();
			var arrdatas = new Array();
			
			$.ajax({
				url: url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){
					if(res.code == 0){
						defer.resolve(res);
						$(".comic-loading").hide();
						$.when(defer.promise()).done(function(res){						
							$.each(res.list, function(index, res){
								if(index > 0){
									arrdatas.push(res);
								}
							})
						})
					}else{						
						$(".comic-loading").append("<p style='position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font-size: .32rem; color: #c7c7c7;'>"+ res.message +"</p>");
					}
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			return arrdatas;
		},		
		getDatas2: function (url){
			var defer = $.Deferred();
			var arrdatas = new Array();
			var titleInfo = this.titleInfo;
			
			$.ajax({
				url: url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){												
					defer.resolve(res);
					$.when(defer.promise()).done(function(res){
						titleInfo = res.data;
						$.each(res.list, function(index, item){
							if(item.url != null){
								arrdatas.push(item);
							}
						});
					})						
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			this.titleInfo = titleInfo;
			return arrdatas;
		},		
		goback: function(){
			window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/');
		}	
	},
	mounted(){		
		
		//漫画阅读器点击 打开 控制面板
		$(".slcf_container").on("click", "#open-ctrl .comic-pic-insert, .comic-loading",function(){
			$(".float-bar").show();
			$(".top").removeClass("downtop");
			$(".bottom").removeClass("downbottom");
			$(".top").addClass("ontop");
			$(".bottom").addClass("onbottom");
			$(".comic-ctrl-box").show();
			$(".prev").css("z-index", 0);
			$(".next").css("z-index", 0);
		})
		//漫画阅读器点击 关闭 控制面板
		$(".comic-ctrl-box").click(function(){		
			$(".top").removeClass("ontop");
			$(".bottom").removeClass("onbottom");
			$(".top").addClass("downtop");
			$(".bottom").addClass("downbottom");
			setTimeout(function(){
				$(".float-bar").hide();
				$(".comic-ctrl-box").hide();
			}, 300);
			$(".prev").css("z-index", 20000);
			$(".next").css("z-index", 20000);
		})
	}
}
</script>

<style>
</style>



