<template>
	<div>
		<Header title="排行"></Header>
		<!-- 漫画排行 -->
		<section id="comicHot" class="ContentBase clearfix">			
			<ul class="rank-init-ul">
				<li class="rank-init-li" v-for="(rank, val) in ranks">
					<router-link :to="'./comic?url='+rank[0].url" class="rank-item-link">
						<div class="item-cover-div">
							<img :src="rank[0].cover" alt="" class="item-cover">
						</div>
						<div class="item-info">
							<strong class="item-title">{{ rank[0].name }}</strong>
							<!-- <small class="item-num">23400</small> -->
							<small class="item-class">{{ rank[0].latest }}</small>
							<small class="item-time">{{ rank[0].time }}</small>
						</div>
						<div :class="setClass(val+1)">{{ rank[0].page }}</div>
					</router-link>
				</li>
			</ul>
		</section>
		<section class="load-more">
			<div class="status-loading" style="display: none;">
				<p class="loading-info">加载中...</p>
			</div>
			<div class="status-over">~已经到底了~</div>
		</section>
		<Footer></Footer>
	</div>
</template>

<script>
	import Header from '../commons/Header_Common.vue'
	import Footer from '../commons/Footer.vue'
	
export default {
	name: 'Rank',
	data() {
		return {
			mhname: this.baseUrl.mhname,
			mhlb: this.baseUrl.mhlb,
			ranks: [],
			
		}
	},
	created(){
		var datas = new Array();
		datas = this.getDatas(this.mhlb+"hot");
		if(datas.length > 1){
			var newdata = new Array();
			var arr = this.commonMethods.numRandom(this.localManga.namelist.length, 6);
			for(var i = 0; i < arr.length; i++){
				newdata.push(this.getNameDatas(this.mhname+this.localManga.namelist[arr[i]].name, (i+1)));
			};
			this.ranks = newdata;
		}
	},
	methods: {		
		setClass: function(val){
			let obj = {'item-number': true};
			obj[`number${val}`] = true;
			return obj;
		},
		getDatas: function (url){
			var defer = $.Deferred();
			var arrdatas = new Array();
			$.ajax({
				url: url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){						
					defer.resolve(res);
					$.when(defer.promise()).done(function(res){
						if(res.code == 0){
							$.each(res.list, function(index, res){
								if(res.url != null){
									arrdatas.push(res);
								}
							})
						}
					})
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			return arrdatas;
		},
		
		getNameDatas: function (url, index){
			var defer = $.Deferred();
			var arrdatas = new Array();
			
			$.ajax({
				url: url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){
					if(res.code == 0){
						defer.resolve(res);							
						$.when(defer.promise()).done(function(res){	
							if(res.list != ""){
								res.list[0].page = index;
								arrdatas.push(res.list[0]);
							}
						})
					}
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			
			return arrdatas;
		}
	},
	mounted(){
		this.commonMethods.toTop();
	},
	components: {
		Header,
		Footer
	}
}
</script>

<style>
</style>



