<template>
	<!-- 猜你喜欢 -->
	<section class="ContentBase clearfix">
		<Loader :isShow="loader"></Loader>
		<div class="main-content">
			<h1>猜你喜欢</h1>
			<div id="nixihuan" class="shop_package clearfix">
				<div class="ship_pic_wrap" v-for="x in nixihuan">
					<router-link :to="'/comic?url='+x[0].url">
						<img :src="x[0].cover">
						<span>{{ x[0].name }}</span>
						<p>{{ x[0].latest }}</p>
					</router-link>
				</div>
			</div>				
		</div>
	</section>
</template>

<script>
import Loader from './Loader.vue'
	
export default {
	name: 'Youlike',
	data() {
		return {
			nixihuan: [],
			loader: true
		}
	},
	created(){
		//猜你喜欢
		this.nixihuan = this.getDatas2(this.baseUrl.mhlb+"shaonianrexue", "nixihuan");
		
		if(this.nixihuan.length > 1){
			var newdata = new Array();
			
			this.loader = false;
			
			var arr = this.commonMethods.numRandom(this.localManga.namelist.length, 6);				
			for(var i = 0; i < arr.length; i++){
				newdata.push(this.getNameDatas(this.nixihuan[arr[i]].name));
			}			
		
			this.nixihuan = newdata;
		}
	},
	watch: {
		"$route": function(){
			//猜你喜欢
			this.nixihuan = this.getDatas2(this.baseUrl.mhlb+"shaonianrexue", "nixihuan");
			
			if(this.nixihuan.length > 1){
				var newdata = new Array();
				var arr = this.commonMethods.numRandom(this.localManga.namelist.length, 6);				
				for(var i = 0; i < arr.length; i++){
					newdata.push(this.getNameDatas(this.nixihuan[arr[i]].name));
				}			
			
				this.nixihuan = newdata;
			}
		}
	},	
	methods: {
		getDatas2: function (url, sel){
			var defer = $.Deferred();
			var arrdatas = new Array();
			
			$.ajax({
				url: url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){
					if(res.code == 0){
						defer.resolve(res);
						$.when(defer.promise()).done(function(res){								
							if(res.list != ""){									
								$.each(res.list, function(index, item){										
									if(item.url != null){
										arrdatas.push(item);
									};
								})
							}else{
								$("#"+sel).parent().hide();
							}
						})
					}
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			
			return arrdatas;
		},
		getNameDatas: function (url){
			var defer = $.Deferred();
			var arrdatas = new Array();
			var mhname = this.baseUrl.mhname;
							
			$.ajax({
				url: mhname+url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){
					if(res.code == 0){
						defer.resolve(res);
						$.when(defer.promise()).done(function(res){	
							if(res.list != ""){																				
								arrdatas.push(res.list[0]);
							}
						})
					}
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			
			return arrdatas;
		}
	},
	components: {
		Loader
	}
}
</script>

<style>
</style>



