<template>
	<section class="ContentBase clearfix">
		<Loader :isShow="loader"></Loader>
		<div class="main-content">
			<h1>国产漫画推荐</h1>
			<div id="guochan" class="shop_package clearfix">
				<div class="ship_pic_wrap" v-for="gcdata in gcdatas">
					<router-link :to="'/comic?url='+gcdata.url">
						<img v-bind:src='gcdata.cover'>
						<span>{{ gcdata.name }}</span>
						<p>{{ gcdata.tag }}</p>
					</router-link>					
				</div>
			</div>				
		</div>
		<div class="main-content">
			<h1>日韩漫画推荐</h1>
			<div id="guochan" class="shop_package clearfix">
				<div class="ship_pic_wrap" v-for="rhdata in rhdatas">
					<router-link :to="'/comic?url='+rhdata.url">
						<img v-bind:src='rhdata.cover'>
						<span>{{ rhdata.name }}</span>
						<p>{{ rhdata.tag }}</p>
					</router-link>
				</div>
			</div>				
		</div>
		<div class="main-content">
			<h1>港台漫画推荐</h1>
			<div id="guochan" class="shop_package clearfix">
				<div class="ship_pic_wrap" v-for="gtdata in gtdatas">
					<router-link :to="'/comic?url='+gtdata.url">
						<img v-bind:src='gtdata.cover'>
						<span>{{ gtdata.name }}</span>
						<p>{{ gtdata.tag }}</p>
					</router-link>
				</div>
			</div>				
		</div>
	</section>	
</template>

<script>
import Loader from './Loader.vue'

export default{
	name: 'RecommenedList',		
	data() {
		return {
			mhname: this.baseUrl.mhname,
			mhlb: this.baseUrl.mhlb,
			gcdatas: [],
			rhdatas: [],
			gtdatas: [],
			datas: ['港台漫画推荐', '日韩漫画推荐', '国产漫画推荐'],
			loader: true
		}
	},
	created() {
		this.startDo()
	},
	methods: {
		getDatas: function(url, sel){
			var defer = $.Deferred();
			var arrdatas = new Array();
			$.ajax({
				url: url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){
					if(res.code == 0){
						defer.resolve(res);
						$.when(defer.promise()).done(function(res){	
							if(res.list != ""){
								if(Number(res.list[0].number) > 1){	
									$.each(res.list, function(index, item){										
										if(item.url != null){
											arrdatas.push(JSON.stringify(item));
										};
									});
								}
							}
						})
					}
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			return arrdatas;
		},
		getNameDatas: function(mhname, url){
			var defer = $.Deferred();
			var arrdatas = new Array();				
			$.ajax({
				url: mhname + url,
				type: "get",
				async: false,
				dataType: "json",
				success: function(res){
					if(res.code == 0){
						defer.resolve(res);
						$.when(defer.promise()).done(function(res){
							if(res.list != ""){																				
								arrdatas.push(res.list[0]);
							}
						});							
					}
				},
				error: function(err, xmlerr, htmlerr){
					console.log(xmlerr);
				}
			});
			return arrdatas[0];
		},
		startDo: function(){
			var mhname = this.baseUrl.mhname;
			//少年
			this.gcdatas = this.$options.methods.getDatas(this.mhlb+"shaonianrexue", "guochan");
			
			//恋爱
			this.gtdatas = this.$options.methods.getDatas(this.mhlb+"lianaishenghuo", "gangtai");
			
			//生活
			this.rhdatas = this.$options.methods.getDatas(this.mhlb+"shenghuomanhua", "rihan");
			
			if(this.gcdatas.length > 1){				
				var newdata = new Array();
				
				this.loader = false;
				
				var arr = this.commonMethods.numRandom(this.localManga.namelist.length, 6);
				for(var i = 0; i < arr.length; i++){
					newdata.push(this.$options.methods.getNameDatas(mhname, this.localManga.namelist[arr[i]].name));					
				}
				this.gcdatas = newdata;
			}
			
			if(this.rhdatas.length > 1){				
				var newdata = new Array();
				var arr = this.commonMethods.numRandom(this.localManga.namelist.length, 6);
				for(var i = 0; i < arr.length; i++){
					newdata.push(this.$options.methods.getNameDatas(mhname, this.localManga.namelist[arr[i]].name));					
				}
				this.rhdatas = newdata;
			}
			
			if(this.gtdatas.length > 1){				
				var newdata = new Array();
				var arr = this.commonMethods.numRandom(this.localManga.namelist.length, 6);					
				for(var i = 0; i < arr.length; i++){
					newdata.push(this.$options.methods.getNameDatas(mhname, this.localManga.namelist[arr[i]].name));					
				}
				this.gtdatas = newdata;					
			}
		}
	},
	mounted(){
		console.log(this.loader);
	},
	components: {
		Loader
	}
}
</script>

<style>
</style>



