<template>
	<header>
		<div class="head_container">
			<div class="head_top">
				<img class="head_logo" src="../../assets/logo-1.png" alt="">
				<a @click="addSearch()" class="search-link" href="javascript: void(0);"></a>
			</div>			
		</div>
		<Search></Search>
	</header>
</template>

<script>
import Search from '../commons/Search.vue'

export default {
	name: 'Header',
	methods:{
		addSearch: function(){
			$(".head_container").next().show();
		}
	},
	components:{
		Search
	}
}
</script>

<style>
</style>



